@font-face {
  font-weight: 400;
  font-family: "Consolas";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Consolas.woff2") format("woff2"), url("../fonts/Consolas.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Consolas";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Consolas-Bold.woff2") format("woff2"), url("../fonts/Consolas-Bold.woff") format("woff"); }

@font-face {
  font-weight: 400;
  font-family: "SegoeUI";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/SegoeUI.woff2") format("woff2"), url("../fonts/SegoeUI.woff") format("woff"); }

@font-face {
  font-weight: 500;
  font-family: "SegoeUI";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/SegoeUI-Italic.woff2") format("woff2"), url("../fonts/SegoeUI-Italic.woff") format("woff"); }

@font-face {
  font-weight: 400;
  font-family: "Merriweather";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Merriweather-Regular.woff2") format("woff2"), url("../fonts/Merriweather-Regular.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font: 400 16px/24px sans-serif; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.game__container {
  display: flex;
  width: 1000px;
  height: 700px;
  margin: 80px auto 0;
  background: url("../img/bg3.jpg") no-repeat center;
  background-size: cover; }

.game__main-screen {
  width: 75%; }

.game__menu-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  padding: 30px 20px;
  background-color: rgba(0, 0, 0, 0.7); }
  .game__menu-screen p {
    font-family: "Consolas", "Arial", sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 18px;
    color: #ffffff; }

.game__counts {
  margin-bottom: 35px; }

.game__main-screen {
  position: relative;
  padding: 30px; }

.game__coffe-list {
  position: absolute;
  top: 30px;
  left: 30px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex; }

.game__moose {
  position: absolute;
  left: 20px;
  bottom: 45px;
  transition: transform 0.08s ease-in-out; }

.game__proogress {
  width: 100%;
  height: 20px;
  border-radius: 0;
  margin-bottom: 35px; }
  .game__proogress::-webkit-progress-bar {
    border-radius: 3px;
    background-color: #ffffff; }
  .game__proogress::-webkit-progress-value {
    border-radius: 3px;
    background-color: #c56d29; }
  .game__proogress::-moz-progress-bar {
    border-radius: 3px;
    background-color: #ffffff; }

.game__btn-start {
  display: block; }

.game__btn-pause, .game__btn-reset {
  display: none;
  margin-bottom: 25px; }

.game__modal {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  display: none;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7); }
  .game__modal p {
    font-family: "Consolas", "Arial", sans-serif;
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    color: #ffffff;
    text-transform: uppercase; }

.btn {
  width: 70%;
  padding: 7px 0;
  font-family: "Consolas", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  color: #c56d29;
  background-color: #ffffff;
  border: 2px solid #c56d29;
  border-radius: 5px;
  cursor: pointer;
  outline: none; }
  .btn:hover, .btn:focus {
    opacity: 0.7; }
  .btn:active {
    opacity: 0.5; }
